export default {
  data() {
    return {
      topTitle: "公告详情",
      list: "",
      title: "",
      add_time: ""
    };
  },
  filters: {
    formatDate: function(value) {
      let val = value * 1000;
      let date = new Date(val);
      let y = date.getFullYear();
      let MM = date.getMonth() + 1;
      MM = MM < 10 ? "0" + MM : MM;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let m = date.getMinutes();
      m = m < 10 ? "0" + m : m;
      let s = date.getSeconds();
      s = s < 10 ? "0" + s : s;
      return y + "-" + MM + "-" + d + " " + h + ":" + m + ":" + s;
    }
  },
  mounted() {
    this.getNotice();
  },
  methods: {
    getNotice() {
      var that = this;
      that
        .$request({
          method: "POST",
          url: "/index/notice"
        })
        .then(function(response) {
          response.data.forEach(val => {
            if (val.notice_id == that.$route.params.id) {
              that.list = val.content.replace(/<img/g,"<img style='max-width:100%;height:auto;'");
              that.title = val.title;
              that.add_time = val.add_time;
            }
          });
        })
        .catch(function(error) {});
    }
  }
};



